body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #293133;
}

.App {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#background {
  height: 100%;
  width: 100%;
  background-image: url(/fcc_frontend_drum_machine/static/media/concert.ef060931.jpg);
  background-size: cover;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

#drum-machine {
  position: absolute;
  max-width: 550px;
  margin: auto;
  background-image: url("/fcc_frontend_drum_machine/static/media/brushed metal.ce522001.jpg");
  background-size: cover;
  border-radius: 20px;
  box-shadow: 3px 3px 15px black;
}

h1 {
  font-family: "Pacifico", cursive;
  margin: auto;
  text-align: center;
  text-shadow: 0px 0px 20px;
  color: white;
  margin: 20px;
}

#display {
  height: 50px;
  line-height: 50px;
  text-align: center;
  margin: 40px;
  font-size: 25px;
  color: #5cc300;
  background-color: #173c12;
  box-shadow: 3px 3px 20px black;
  border-radius: 2px;
  box-shadow: inset 1px 1px 5px black;
  margin-top: 20px;
  font-family: "Oswald", sans-serif;
}

#button-container {
  display: grid;
  grid-template: 33% 33% 33% / 33% 33% 33%;
  justify-items: center;
}

button:hover {
  cursor: pointer;
}

.drum-pad {
  width: 100px;
  height: 100px;
  margin: 20px;
  border-radius: 5%;
  border: none;
  box-shadow: 3px 3px 20px;
  font-size: 25px;
  background-color: rgba(255,244,0,1);
  font-family: "Oswald", sans-serif;
}

.drum-pad:focus {
  outline: none;
}

@media screen and (max-width: 420px) {
  #display {
    margin-bottom: 20px;
  }

  #button-container {
    margin-top: 20px;
  }

  #drum-machine {
    width: 100%;
    border-radius: 0;
  }

  .drum-pad {
    width: 75px;
    height: 75px;
    margin: 10px;
  }
}

@media screen and (max-height: 500px) {
  #drum-machine {
    height: 100%;
  }

  #button-container {
    margin-top: 30px;
  }
}
